import { LineStyle } from './Component'

/**
 * 
 */
export const IndicatorDisplayRule = {
  /**
   * 
   */
  ALWAYS: 'always',
  /**
   * 
   */
  FOLLOW_CROSS: 'follow_cross',

  /**
   * 
   */
  NONE: 'none'
}

class Tooltip {
  constructor () {
    /**
     * 
     */
    this.textSize = 12

    /**
     * 
     */
    this.crossLine = {
      style: LineStyle.SOLID,
      dashValue: [8, 8],
      size: 1,
      color: '#505050',
      text: {
        color: '#EDEDED',
        size: 12,
        rectStrokeLineSize: 1,
        rectStrokeLineColor: '#EDEDED',
        rectFillColor: '#505050',
        margin: 4,
        valueFormatter: null
      }
    }

    /**
     * 
     */
    this.generalData = {
      labels: ['', '', '', '', ''],
      values: null,
      valueFormatter: null,
      text: {
        size: 12,
        color: '#898989',
        margin: 20
      }
    }

    /**
     * 
     */
    this.indicatorData = {
      displayRule: IndicatorDisplayRule.ALWAYS,
      valueFormatter: null,
      text: {
        size: 12,
        margin: 20
      }
    }
  }
}

export default Tooltip
