import { LineStyle } from './Component'
import { YAxisTextPosition } from './YAxis'

class Axis {
  constructor () {
    /**
     * 
     */
    this.display = true
    /**
     * 
     */
    this.color = '#707070'

    /**
     * 
     */
    this.axisLine = {
      display: true,
      color: '#707070',
      size: 1
    }

    /**
     * 
     */
    this.tickText = {
      display: true,
      position: YAxisTextPosition.OUTSIDE,
      color: '#707070',
      size: 12,
      margin: 3,
      valueFormatter: null
    }

    /**
     * tick
     */
    this.tickLine = {
      display: true,
      size: 3,
      color: '#707070'
    }

    /**
     * 
     */
    this.separatorLine = {
      display: false,
      size: 1,
      color: '#B8B8B8',
      style: LineStyle.DASH,
      dashValue: [8, 8]
    }
  }
}

export default Axis
